<template>
  <admin-template
    pageName="Minha agenda"
    alias="schedule"
    :breadcrumbs="breadcrumbConfig"
    :content="showContent"
    :btnRegister="!showContent"
  >
    <schedule-group />
  </admin-template>
</template>

<script>
import AdminTemplate from '@/components/admin/template/Template'
import ScheduleGroup from '@/components/admin/schedules/schedule-group/ScheduleGroup'
import BarberMixin from '@/mixins/BarberMixin'

export default {
  name: 'Schedule',
  components: { AdminTemplate, ScheduleGroup },
  mixins: [ BarberMixin ],
  computed: {
    breadcrumbConfig () {
      if (this.barber.barbershop_id) {
        return [
          { text: "Dashboard", disabled: false, href: "/admin" },
          { text: "Minha agenda", disabled: true, href: "/admin/minha-agenda" }
        ]
      }
      
      return []
    },
    showContent () {
      return this.barber.barbershop_id
    }
  }
}
</script>